.gallery-view {
  padding: 20px;
  width: 100%;
  overflow: auto;
}

.gallery-title{
    text-align: center;
}

.gallery-video {
  width: 440px;
  height: 220px;
  display: inline-block;
  margin: 20px;
  margin-bottom: 40px;
}
.gallery-video-intro{
    margin-top: 10px;
    text-align: left;
}
.gallery-video-intro p{
    margin: 0;
}

.video-react .video-react-big-play-button{
    background-color: rgb(255, 77, 79) !important;
}

.gallery-video-title{
    font-weight: 800;
    font-size: 1.6rem;
}

.gallery-video-source{
    color: #606060;
    margin-bottom: 20px;
}